<template>
    <div class="main">

        <div class="outer" id="canvas">
        <div class="contant" >
            <div class="top">
                <p class="name">{{name}}</p>
                <p>当年上学座位图</p>
            </div>
            <div class="center" id="center">
            </div>
            <div class="bottom">
                <img  class='left' src="../images/img_logo-result.png"/>
                <img  class="right" src="../images/二维码/qrcode.png"/>
                <p class="sentance">长按生成我的座位图</p>
            </div>
        </div>
        </div>



        <div class="save">长按上方保存图片</div>
        <div class="bottom-button">点击下载小睡眠APP</div>
    </div>
</template>

<script>
    import Html2Canvas from 'html2canvas'


    export default {
        name: "result",
        data(){
          return {
              name: 'aaasd'
          }
        },
        methods:{
            setImgSize(img, id){
                const img_scale = img.height / img.width
                const content_scale = document.getElementById(id).offsetHeight + ' ' + document.getElementById(id).offsetWidth

                if(img_scale < content_scale){  img.width = document.getElementById(id).offsetWidth - 10 }else{
                    img.height = document.getElementById(id).offsetHeight -10
                }
            },
            append(){
                const dom = document.getElementById('canvas')
                const img = new Image()
                img.src = localStorage.getItem('canvasSrc')
                this.setImgSize(img, 'center')

                document.getElementById('center').appendChild(img)
                //这里告诉我不可以操作dom 因为也是获取不到变化的
                //这个onclone 是修改之后的样式
                //你犯错误了 写成clone 是带on的 onclone啊

                //还有一个错误就是onclone只可以改变你所传入dom的内容 而不能直接修改dom
                //要dom的子元素才可以


                Html2Canvas(dom, {onclone:(document)=>{
                    document.getElementsByClassName('contant')[0].style.visibility = 'visible'
                        //console.log(document.getElementsByClassName('top')[0].style)
                        //.style.visibility = 'visible'
                    //添加上面修改好的图片
                }}).then((canvas)=>{
                    const img = new Image()
                    img.src = canvas.toDataURL()
                    img.id = 'resultId'
                    img.style.width = '100%'
                    //img.width = 200
                    dom.innerHTML = ''
                    dom.appendChild(img)
                })

            },
            getName:new Promise(function(resolve){
                const xml = new XMLHttpRequest()
                alert(sessionStorage.getItem('code').replace(/code=/,'')+'这是获取code的啊')
                xml.open('GET', 'https://api.debug.psy-1.com' +'/web/v1/wechat/user?code=' + sessionStorage.getItem('code').replace(/code=/,''))
                xml.onreadystatechange = ()=>{
                    if(xml.readyState === 4 && xml.status === 200){
                        let getJson = JSON.parse( xml.response ).data.nickname
                        alert('获取名称了')
                        localStorage.setItem('name', getJson)
                        this.name = getJson
                        this.$nextTrick(resolve())
                    }
                }
                xml.send()
            }),
            getNameInit(){
                if(this.isWeiXin()){
                    const name = localStorage.getItem('name')
                    if( !name ){  this.getName().then(this.append())  }else{
                        this.name = name
                        this.$nextTrick( this.append )
                    }
                }
            },
            isWeiXin(){
                return /micromessenger/.test(navigator.userAgent.toLowerCase())
            }
        },
        mounted(){
            //this.append()
            this.getNameInit()
            //alert('什么回事哦？')
        }
    }
</script>

