<template>
    <div class="main" id="playing">
        <div class="top" >
            <router-link to="/playing/s">
            <div @click="change" class="left" id="left">
                小班级
            </div>
            </router-link>

            <router-link to="/playing/b">
            <div @click="change" class="right background-fontColor" id="right">
                大班级
            </div>
            </router-link>
        </div>

        <div id="center" class="center">
            <div class="blackboard"></div>
            <div class="teacher"></div>
            <router-view />
            <div v-if="result" id="result" class="bottom-desc"><span id="lb" class="lb">{{lb}}</span><span id="desc">{{result}}</span></div>
        </div>

        <div class="bottom">
            <p @click="submit" class="btn">确认座位</p>
            <p class="sentence">·选中你上学座位，最多五个·</p>
        </div>

        <Alert :toggle="toggle" >{{ alert_sentance }}</Alert>
    </div>
</template>

<script>
    import Alert from '../components/Alert.vue'
    import Html2Canvas from 'html2canvas'

    export default {
        name: "playing",
        data(){
            return {
                toggle: true,
                alert_sentance: 'ha呵kk',
                count: [],
                result:'',
                lb:'',
                show: false
            }
        },
        methods:{
            weixinSDK(url){
                const getappid = new XMLHttpRequest();
                getappid.open('GET', url + '/web/v1/wechat/config?debug=1&url=' + url)
                getappid.onreadystatechange = function(){
                    if(getappid.readyState === 4 && getappid.status === 200){
                        console.log('sdk ok')
                        //我觉得有可能是hash的问题
                        //alert('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx632d4c99bd681cf3&redirect_uri=' + encodeURI( location.href.split('#')[0] ) +'&response_type=code&scope=snsapi_userinfo#wechart_redirect')

                        window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx632d4c99bd681cf3&redirect_uri=' + encodeURI( location.href.split('#')[0] ) +'&response_type=code&scope=snsapi_userinfo#wechart_redirect'
                    }
                }
                getappid.send()
            },
            weatherCode(){
                const QRblock = /code=(.+?)&/
                const code = window.location.href.match(QRblock)
                //code = code.replace('code=','')
                //没有理由啊 怎么回事 获取到code=?asdasdasd的啊 ....我日
                //alert( code )
                if(code){  return code } else{
                    return false
                }
            },
            isWeiXin(){
                return /micromessenger/.test(navigator.userAgent.toLowerCase())
            },
            weixinInit(url){
                if( this.isWeiXin() ){
                    let code = this.weatherCode()
                    if(!code){  this.weixinSDK(url)  }
                    else{ sessionStorage.setItem('code', code)  }
                }
            },
            change(e){
                const left = document.getElementById('left').classList
                const right = document.getElementById('right').classList
               left.remove('background-fontColor');right.remove('background-fontColor')
               e.target.classList.add('background-fontColor')
            },
            select(){
                const that = this
                document.getElementById('center').addEventListener('click', function(e){
                    const classlist = e.target.classList
                    if(classlist.contains('bgcolor')){ classlist.remove('bgcolor');console.log('删除') }
                    else if(classlist.contains('itemlist')){
                        const bgcolor_list = document.getElementsByClassName('bgcolor').length
                        console.log('就是看看List的个数吧' + bgcolor_list)
                        if( bgcolor_list < 3 ) {  classlist.add('bgcolor')  }else{
                            that.alertShow('最多可选三个座位(～￣▽￣)～')
                        }
                   }
                } ,false)
            },
            alertShow(sentance){
                this.toggle = !this.toggle
                this.alert_sentance = sentance
            },
            randomNum(minNum,maxNum){
                switch(arguments.length){
                    case 1:
                        return parseInt(Math.random()*minNum+1,10);
                        break;
                    case 2:
                        return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
                        break;
                    default:
                        return 0;
                        break;
                }
            },
            getIndexOfElement(className, containName){
                console.log(className+ containName)
                const dom = document.getElementsByClassName( className )
                for(let i = 0; i < dom.length; i++){
                    if( dom[i].classList.contains( containName ) ) this.count.push(i+1)
                }
                console.log(this.count)
            },
            getScore(count){
                let score = 0;
                for(let i = 0; i<count.length; i++){
                    if([12,13,14,15,20,21,22,23,28,29,30,31].includes( count[i] )){score += 150;} else
                    if([2,3,10,11,18,19,26,27,34,35,42,43].includes( count[i] )){score += 115;} else
                    if([8,9,16,17,24,25,32,33,40,41,48,49].includes( count[i] )){score += 80;} else
                    if([36,37,38,39,44,45,46,47].includes( count[i] )){score += 50;} else
                    if([4,5,6,7].includes( count[i] )){score += 25;}
                }
                console.log(score)
                return score
            },
            getLevel(score){
                if(this.count.length == 1){  //好恶心 找个时间摆脱这种不必要的代码 ifelse
                    if(150>= score  &&score >=136) return 10;
                    if(135>= score  &&score >=121) return 9;
                    if(120>= score  &&score >=106) return 8;
                    if(105>= score  &&score >=91) return 7;
                    if(90>= score &&score >=76) return 6;
                    if(75>= score &&score >=61) return 5;
                    if(60>= score &&score >=46) return 4;
                    if(45>= score &&score >=31) return 3;
                    if(30>= score &&score >=16) return 2;
                    if(15>= score &&score >=0) return 1;
                }

                if(this.count.length == 2){
                    if(300>= score &&score >=271) return 10;
                    if(270>= score &&score >=241) return 9;
                    if(240>= score &&score >=121) return 8;
                    if(210>= score &&score >=181) return 7;
                    if(180>= score &&score >=151) return 6;
                    if(150>= score &&score >=121) return 5;
                    if(120>= score &&score >=91) return 4;
                    if(90>= score &&score >=61) return 3;
                    if(60>= score &&score >=31) return 2;
                    if(30>= score &&score >=0) return 1;
                }

                if(this.count.length == 3){
                    if(450>= score &&score >=406) return 10;
                    if(405>= score &&score >=361) return 9;
                    if(360>= score &&score >=316) return 8;
                    if(315>= score &&score >=271) return 7;
                    if(270>= score &&score >=226) return 6;
                    if(225>= score &&score >=181) return 5;
                    if(180>= score &&score >=136) return 4;
                    if(135>= score &&score >=91) return 3;
                    if(90>= score &&score >=46) return 2;
                    if(45>= score &&score >=0) return 1;
                }
                //就写一篇怎么摆脱这种恶心不利于维护的代码吧 ..  估计是用编译模板eval( '(' ')' )

            },
            getLabel(level){
                if( level === 10) return ['痴狂学魔','天生丽质','模范学生','争分夺秒','品学兼优', '自学成才',	'名列前茅',	'以梦为马',	'满腹经纶'][this.randomNum(0,8)] ;
                if( level === 9)  return ['完美学神',	'一表人才',	'优秀学生',	'闻鸡起舞',	'智商爆表', '幽默风趣',	'豁达大度',	'手不释卷',	'金榜题名'][this.randomNum(0,8)] ;
                if( level === 8)  return ['至尊学霸',	'玉树临风',	'三好学生',	'废寝忘食',	'迎难而上', '魅力十足',	'笨鸟先飞',	'聪明伶俐',	'积极进取'][this.randomNum(0,8)] ;
                if( level === 7)  return ['伪学霸',	'情窦初开',	'逗比学委',	'勤学好问',	'冷静聪慧', '坚持不懈',	'小心谨慎',	'心地善良',	'理性派'][this.randomNum(0,8)] ;
                if( level === 6)  return ['努力范',	'眉清目秀',	'获奖无数',	'刻苦钻研',	'多谋善断', '平易近人',	'朝气蓬勃',	'沉静寡言',	'天真无邪'][this.randomNum(0,8)] ;
                if( level === 5)  return ['文艺范',	'才貌双全',	'优秀班干',	'天资聪颖',	'情商超高', '赤子之心',	'头脑灵活',	'快言快语',	'行动力强'][this.randomNum(0,8)] ;
                if( level === 4)  return ['抱佛脚',	'风度翩翩',	'进步之星',	'积极乐观',	'初心依旧', '鬼点子多',	'调皮捣蛋',	'口直心快',	'不拘小节'][this.randomNum(0,8)] ;
                if( level === 3)  return ['偏科大神',	'清新脱俗',	'小组长',	'马马虎虎',	'创意新颖', '阳光开朗',	'意气用事',	'多愁善感',	'口齿伶俐'][this.randomNum(0,8)] ;
                if( level === 2)  return ['运动奇才',	'俊俏可人',	'课代表',	'八卦之王',	'自由随心', '特立独行',	'重情重义',	'放荡不羁',	'聪明过人'][this.randomNum(0,8)] ;
                if( level === 1)  return ['最强学渣',	'倾国倾城',	'生活委员',	'出类拔萃',	'青春叛逆', '最佳损友',	'兴趣广泛',	'逆袭为王',	'口齿伶俐'][this.randomNum(0,8)] ;
            },
            getDescription(level){
                if( level === 10) return ['当年的你，是天才型学霸，讨论题目就像华山论剑，是全班膜拜的对象。',
'当年的你，是完美型学霸，如果要给学习加个期限，你希望是一万年。 ',
'当年的你，是自学成才型学霸，上课大概只是旁听，毕竟对你来说没有难度。'][this.randomNum(0,2)] ;
                if( level === 9)  return ['当年的你，是隐匿在人群中才貌双全的学霸，是所有异性暗恋的对象。',
'当年的你，自带学霸基因，整日沉迷于学习无法自拔。',
'当年的你，每次考试都能名列前茅，大概是用了《5年考题3年模拟》。'][this.randomNum(0,2)] ;
                if( level === 8)  return ['当年的你，是勤奋型学霸，一日不做题便浑身难受。',
'当年的你，是同桌的心上人，在做题中慢慢靠近，得到情感的升华。',
'当年的你，是阳光型学霸，学习对你来说是世上最快乐的事。'][this.randomNum(0,2)] ;
                if( level === 7)  return ['当年的你，是学霸眼中的伪学霸，笨鸟先飞，勤奋刻苦。',
'当年的你，是伪学霸中的战斗机，离学霸只有一步之遥。',
'当年的你，熬夜学习是常态，困意来袭却逞强：“扶朕起来，朕还能学习。'][this.randomNum(0,2)] ;
                if( level === 6) return ['当年的你，是老师眼中的优等生，头脑灵活，有过人的才艺。',
'当年的你，是品学兼优的班干部，领导能力强，是老师的好帮手。',
'当年的你，为修成学霸曰：“天将降大任于斯人也，必先关其机，停其流量。'][this.randomNum(0,2)] ;
                if( level === 5) return['当年的你，德智体美劳全面发展，天资聪颖，自制力非常好。',
'当年的你，是偏科奇才，不屑与学霸相比，但却最怕开家长会。 ',
'当年的你，经常和同桌吐槽：“这个老师不太行，怎么讲都讲不明白。'][this.randomNum(0,2)] ;
                if( level === 4) return['当年的你，是班上的逗比，但逢考试必定是一夜复习两茫茫，看一句、忘三行。',
'当年的你，是班上的八卦之神，洞察力超强，所有的秘密都逃不过你的双眼。',
'当年的你，及格，你所欲也；偷懒，亦你所欲也，无奈两者不可兼得。'][this.randomNum(0,2)] ;
                if( level === 3) return['当年的你，是特困生中的战斗机，虽然考前临时抱佛脚，但每次都能侥幸及格。',
'当年的你，上课就像听天书，宁愿在梦中指点江山，挥斥方遒。',
'当年的你，天不怕地不怕，但会和老师卖乖：“纸上得来终觉浅，恳请老师划重点。'][this.randomNum(0,2)] ;
                if( level === 2) return['当年的你，是学渣中的老司机，但你和学霸有个共同点：写作业不用翻书。',
'当年的你，青春叛逆，调皮捣蛋。学霸在复习考试，你却才开始预习。',
'当年的你，考试就像在做视力检查，看不到的全靠蒙，及格全凭运气。'][this.randomNum(0,2)] ;
                if( level === 1) return['当年的你，是不良少年，是老师的眼中钉，但懂得举一反三，逆向思维超强。',
'当年的你，是学渣中的大哥大，但你有个愿望，希望老师高抬贵手，让你及格。',
'当年的你，考试完全避开了正确选项，全凭想象力在答题。'][this.randomNum(0,2)] ;
            },
            changeToIMG(){
                const dom = document.getElementById('center')

                Html2Canvas(dom, {onclone: (document)=>{
                    //document.getElementById('center').display = 'flex'
                        //dom.display = 'none' ;
                        document.getElementById('result').style.display = 'initial'
                        document.getElementsByClassName('need')[0].id ='changeFirstColor'
                        for(let i of document.getElementsByClassName('decorations')){
                            i.style.display = 'block'
                        }

                    }}).then((canvas)=>{
                    localStorage.setItem('canvasSrc', canvas.toDataURL() )
                    this.$router.push({path: '/result'})
                })

            },
            submit(){
                console.log(this.count.length)
                this.getIndexOfElement('itemlist', 'bgcolor')
                if(this.count.length == 0) this.alertShow('至少选择一个座位(～￣▽￣)～')
                else{
                    const level = this.getLevel( this.getScore(this.count) )
                    const lb = this.getLabel (level)
                    const dc = this.getDescription(level)
                    const lbDom = document.getElementById('lb')
                    const descDom = document.getElementById('descDom')

                    this.lb = lb  //我发现vue是让html2canvas感受不到数据的
                    this.result = dc

                    this.$nextTick( this.changeToIMG )  //所以我觉得是没有修改dom的问题
                    //this.changeToIMG()
                    //this.$router.push({path: '/result'})
                }
            }

        },
        components:{
            Alert
        },
        mounted(){
            this.select()
            this.weixinInit('https://api.debug.psy-1.com')
        }
    }
</script>

