import Vue from 'vue'
import Router from 'vue-router'
import FristPage from './pages/frist_page.vue'
import Playing from './pages/playing.vue'
import Result from './pages/result.vue'


Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/playing',
      name: 'playing',
      component: Playing,
      children: [{
          path: 'b',
          component:  () => import(/* webpackChunkName: "about" */ './components/BigGrid.vue')
      },{
          path: 's',
          component:  () => import(/* webpackChunkName: "about" */ './components/SmallGrid.vue')
      }
      ]
    },
    {
        path: '/',
        name: 'frist_page',
        component: FristPage
    },
    {
          path: '/result',
          name: 'result',
          component: Result
    },
    {
      path: '/aboutasdas',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue') //留着这个 免得到时候异步加载的时候又去找代码
    }
  ]
})
