<template>
  <div class="main">
    <img class="top-left" src="../images/img_logo-result.png"/>
    <img class="top-right" :src="tgImg" @click="$data.toggle = !$data.toggle"/>
    <img class="center-img" src="../images/img_picture.png"/>
    <img class="bottom" src="../images/img_openning-adv.png"/>
    <router-link to="/playing/b">
      <img class="btn" id="btnBegin" src="../images/btn_start.png"/>
    </router-link>
  </div>
</template>

<script>
  export default {
      name: 'frist_page',
      data(){
          return {
              toggle: true,
              on: require('../images/btn_music-on.png'),
              off: require('../images/btn_music-off.png')
          }
      },
      computed: {
          tgImg(){
              if(this.toggle){
                  this.music(true)
                  return this.on
              }
              else{
                  this.music(false)
                  return this.off
              }
          }
      },
      methods:{
          music(toggle){
             const audio = document.getElementById('myaudio')
             if(audio) toggle  ? audio.play() : audio.pause()  //还没有获取到dom
          },
          initMusic(){
              //这是为了解决safari不能自动播放的问题的 因为用户没有交换 所以我决定放在touchstart那 然后添加toggle是为了一开始用户还没有进行交换就关闭Music了
              //还有我把他放在全局组件那是为了后面交换触发音乐
              //console.log('touchstart')
              if(this.toggle) {
                  setTimeout(()=>{
                      const audio = document.getElementById('myaudio')
                      audio.play()
                  },1000)  //感觉加上1000ms，用户有概率接触屏幕两次 从而触发
              }
          }
      },
      mounted(){
         // setTimeout(this.initMusic(), 1000)
          document.addEventListener('touchstart', this.initMusic, false)
      }
  }
</script>



<style lang="scss" scoped>
  .main {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    //background: url(../images/img_bg.jpg); 全局好
    //background-size: 100% 100%;  全局大法
    .center-img{
      width: 100vw;
    }

    %same{
      position: absolute;
    }
    $height-padding: 1em;
    .top-left{
      @extend %same;
      top: $height-padding;
      left: 1em;
      height: 2em;
    }
    .top-right{
      @extend %same;
      width: 3em;
      top: $height-padding;
      right: 1em;
    }
    $bottom_height: 3em;
    $bottom_bottom: 1em;
    %transitionX{
      right: 50vw;
      transform: translateX(50%);
    }
    .bottom{
      @extend %same;
      @extend %transitionX;
      height: $bottom_height;
      bottom: $bottom_bottom;
    }
    .btn{
      @extend %same;
      @extend %transitionX;
      height: 2em;
      bottom: $bottom_height + $bottom_bottom;
    }


  }
</style>
